<template>
  <swiper 
    :modules="modules"
    :navigation="true"
    :autoplay="{
      delay: 2500,
      disableOnInteraction: false
    }" 
    :pagination="{ clickable: true }"
  >
    <swiper-slide v-for="(item,i) in imgs.banners" :key="i">
      <img :src="item.pic" alt="抱歉，图片加载失败，请检查网络">
    </swiper-slide>
  </swiper>
</template>

<script>
import SwiperClass, { Pagination ,Navigation,Autoplay} from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

import 'swiper/css'
import 'swiper/css/pagination'
import { onBeforeMount,reactive } from 'vue';
import {getBanner} from '../assets/apifuntion'

  export default {
    components: {
      Swiper,
      SwiperSlide
    },
    setup() {
      // 轮播图图片路径，默认为同一张，等待数据覆盖，如果每一张图一样，说明数据加载失败
      var imgs=reactive({
          banners:[
            {pic:require('../assets/1.jpg')},
          ]
      });
      // 生命周期挂载之前发出数据请求，并且将数据覆盖默认数据
      onBeforeMount(async()=>{
        // 异步发送数据,将数据中的所需部分取出并赋值给响应式数据的内层，避免被覆盖后失去响应式，并且数据请求失败有默认值，避免报错影响布局
        imgs.banners=await(await getBanner(1)).data.banners;
      });
      return {
        imgs,
        modules: [Pagination,Navigation,Autoplay]
      } 
    },
    
  } 
</script>

<style lang="less" scoped>
  .swiper-slide{
    padding: 0.2rem;
    height: 3rem;
    img{
      border-radius: 0.3rem;
      width: 100%;
    }
  }
</style>